*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  
  font-family: 'Outfit', 'BlinkMacSystemFont', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1{
  font: size 6rem;
}
.app{
  width: 100%;
  height: 100vh;
  position: relative;
  color:#fff ;
  background-color: rgba(0,0,0,0.4);

}
.app:before{
  content: '';
  background: url('./assets/clouds.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

p{
  font-size: 1.6rem;
}
.container{
  max-width: 700px;
  height: 700px;
  margin :auto;
  padding:0 1rem;
  position: relative;
  top:10%;
  display: flex;
  flex-direction: column;
  justify-content:spa;


}

.app.top{
  width: 100%;
  margin: 1rem auto;
}
.description{
  position: relative;
  right: -90%;
  transform-origin:0 0;
  transform:rotate(269deg);
}

.bottom{
  display:flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  background-color: rgba(255,255,255,0.2);
  border-radius: 12px;
}

.bold{
  font-weight: 700;
  
}
.search{
  text-align: center;
  padding: 1rem;


}
input{
  padding: .7em 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255,0.8);
  background: rgba(255,255,255,0.1); 
  color: #fff;
}